.testimonial {
	text-align: center;
}

.testimonial-author {
	font-size: 1.2em;
	padding: 0.5em 0;
}


.testimonial-content {
	font-size: 1em;
	padding: 0 4em;
	font-style: italic;
}


.testimonial-img {
    width: fit-content;
    max-width: 300px;
	max-height: 300px;
    margin: 0 auto;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 50%;
    transition: all 0.4s ease;
}

@media screen and (max-width: 768px) {
	.testimonial-content {
		padding: 0 0.5em;
	}
}